import { Component, OnInit } from '@angular/core';
import { ModelService } from 'src/app/services/model.service';
import { FormControl } from '@angular/forms';
import { saveAs } from 'file-saver';
import { faDownload, faEdit, faPlus } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-models-list',
  templateUrl: './models-list.component.html',
  styleUrls: ['./models-list.component.css'],
})
export class ModelsListComponent implements OnInit {
  faDownload = faDownload;
  faEdit = faEdit;
  faPlus = faPlus;

  models: any;
  name = new FormControl('');

  constructor(private modelService: ModelService) {}

  ngOnInit(): void {
    this.retrieveModels();
  }

  retrieveModels(): void {
    this.modelService.getAll().subscribe(
      (data) => {
        this.models = data;
        console.log(data);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  onKeydown($event: KeyboardEvent): void {
    if ($event.key === 'Enter') {
      this.searchName();
    }
  }

  getSize(model) {
    return Object.keys(model.versions).length;
  }

  searchName(): void {
    this.modelService.findByName(this.name.value).subscribe(
      (data) => {
        this.models = data;
        console.log(data);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  download(model): void {
    const version = this.getLatestVersion(model);
    console.log(version);
    this.modelService.downloadVersion(version.id).subscribe(
      (data) => {
        console.log(data);
        saveAs(data, `${version.name}.${version.type}`);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  getLatestVersion(model) {
    return model.versions[model.versions.length - 1];
  }
}
